import React from 'react';
import Avatar from './Avatar';
import NombreUsuario from './NombreUsuario'


const Header = () => {
    const user = { name: "Wanda", apellido: "Rocha" };
    return ( <header className="encabezado" >
        <h1>ComIT</h1>
        <div>
            <NombreUsuario user ={user}/>
            <Avatar />
        </div>
    </header> );
}

// JSX - azucar sintactico seria como una extension de js pero mucho mas optimizada
// JSX - JAVASCRIPT Y HTML/XML
export default Header;