import React, {useState} from 'react';
import Table from './Table';

class Reloj extends React.Component {
    constructor (props) {
        super(props);
        this.state = {date: new Date()};
    }

    componentDidMount() {
        this.timerID = setInterval (()=> this.clock(),1000);
    }

    clock() {
        this.setState ( {
            date: new Date()
        });
    } 

    render() {
        return(
        <div><h2>La hora es: {this.state.date.toLocaleTimeString()} </h2></div>
        )
    }
}


const Section = () => {
    const [datos,setDatos] = useState('');

        function cambios(e) {
            setDatos(e.target.value);
        }

        function click(e) {
            e.preventDefault();
            document.getElementById("parrafo").textContent = datos;
        }

    return (
    <section className="section">
        <Reloj/>
        <h1>Hola estoy trabajando con React!</h1>
        <p>
            Es una libreria de Javascript para crear interfacez de usuario (UI) en el front-end.
        </p>

        <section>
            <input type="text" value={datos} onChange={cambios}/>
            <button onClick={click}>Enviar</button>
            <p id="parrafo"></p>
        </section>

        <Table/>
    </section> );
}
 
export default Section;
